.CountryAbout {
  position: relative;
  min-height: calc(100vh - 4.75rem);
  background-image: linear-gradient(rgba(122, 77, 64, 0.83),
  rgba(122, 77, 64, 0.84)), url(../../public/img/background/background_03.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
}

.CountryAbout h1 {
  padding-top: 3rem;
  font-size: 3rem;
  font-weight: 400;
  color: #eaeaea;
  text-align: center;
}

.CountryAbout p {
  margin-top: 3rem;
  font-size: 1.75rem;
  text-indent: 3rem;
  line-height: 3rem;
  color: #eaeaea;
  text-align: justify;
}

.js-project {
  text-decoration: none;
  color: #eaeaea;
}

.js-project:hover {
  color: #61ff10;
}

@media (max-width: 1080px) {
  .CountryAbout h1 {
    padding-top: 1.25rem;
    font-size: 2.25rem;
  }
  .CountryAbout p {
    margin-top: 1.125rem;
    font-size: 1.5rem;
    line-height: 2rem;
    color: #eaeaea;
    text-align: justify;
  }
}

@media (max-width: 800px) {
  .CountryAbout h1 {
    padding-top: 1.25rem;
    font-size: 2rem;
  }
  .CountryAbout p {
    margin-top: 1rem;
    font-size: 1.25rem;
    line-height: 1.75rem;
    color: #eaeaea;
    text-align: justify;
  }
}